<template>
    <div class="class3__wrapper">
      <template v-for="(class1, class1_index) in menuLists" :key="class1_index">
        <template v-for="(class2, class2_index) in class1.class2s" :key="class2_index">
            <template v-for="(class3, class3_index) in class2.class3s" :key="class3_index">
                <template v-if="selectClass.class1==class1.name && selectClass.class2==class2.name">
                    <Class3Show
                    class="class3__item"
                    :class3="class3.name"
                    :price="class3.price"
                    :class4s="class3.class4s"
                    @add_cart="add_cart"
                />
                </template>
            </template>
        </template>
      </template>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
import Class3Show from "../molecules/Class3Show"

export default {
    components: {
        Class3Show,
    },
    data: () => {
    return {
        animationTime: getComputedStyle(document.documentElement).getPropertyValue('--animation-time')
        }
    },
    watch: {
        lightBox:{
            handler(){
                if (this.lightBox == 'Class3s'){
                    setTimeout(() => 
                        this.imgMountCheck(),
                    this.animationTime)
                }
            }
        },
        selectClass:{
            handler(){
                setTimeout(() => 
                    this.imgMountCheck(),
                this.animationTime)
            },
            deep: true,
            immediate: true,
        },
        sideNav:{
            handler(){
                setTimeout(() => 
                    this.imgMountCheck(),
                this.animationTime)
            },
            deep: true,
            immediate: true,
        },
        menuLists:{
            handler(){
                setTimeout(() => 
                    this.imgMountCheck(),
                this.animationTime)
            },
            deep: true,
            immediate: true,
        },
    },
    mounted: function () {
        window.addEventListener("resize", ()=>{
            let timeoutID = 0;
            let delay = 500;
            clearTimeout(timeoutID);
            timeoutID = setTimeout(()=>{
                this.makePageCardGridLayout()
    
            }, delay);
        }, false);
    },
    computed: {
        ...mapGetters([ 'menuLists' ,'selectClass', 'sideNav', 'lightBox']),
    },
    methods: {
        ...mapActions([ 'addCart', 'cartBallPositionUpdate' ]),
        add_cart(event, menu){
            const add_cart_menu = {
                class1: this.selectClass.class1,
                class2: this.selectClass.class2,
                class3: menu.class3,
                setmenu_values: [],
                quantity: menu.quantity,
                price: menu.price,
            }
            const position_and_menu = {position: {x: event.pageX - event.offsetX, y: event.pageY - event.offsetY}, menu: add_cart_menu}
            this.cartBallPositionUpdate(position_and_menu)
        },
        imgMountCheck(){
            const images = document.querySelectorAll('img');
            let loadCnt = 0;
            if (images.length > 0) {
                for (const img of images) {
                    let image = new Image();
                    image.src = img.src;
                    image.addEventListener('load', () => {
                        loadCnt++;
                        if (loadCnt == images.length) {  // ç”»åƒãŒå…¨éƒ¨èª­ã¿è¾¼ã¾ã‚ŒãŸã‹åˆ¤å®šã—ã¦ã„ã¾ã™
                            this.makePageCardGridLayout();  // ã“ã®å¾Œã€ã“ã®ãƒ¡ã‚½ãƒƒãƒ‰ã®ä¸­èº«ã‚’æ›¸ãã¾ã™
                        }
                    })
                }
            }else{
                this.makePageCardGridLayout()
            }
        },
        makePageCardGridLayout() {
            console.log('makePageCardGridLayout')
            let evenPosX = 0;
            let evenPosY = 0;
            let oddPosY = 0;
            const PosX = 0;
            let PosY = 0;
            let sh
            const marginBottom = 10
            const class3__items = document.getElementsByClassName('class3__item');
            if(class3__items.length > 0) {
                let gridWindow;
                for (let i = 0; i < class3__items.length; i++) {
                    i == 0 ? gridWindow = class3__items[0].closest('.class3__wrapper') : i; // Gridã®è¦ªè¦ç´ ã‚’å–å¾—ã—ã¾ã™
                    if (window.matchMedia('(max-width: 959px)').matches) {
                        console.log('under 959px')
                        class3__items[i].setAttribute("style", "transform: translateX(" + PosX + "px) translateY(" + PosY + "px)");  // ã‚¹ã‚¿ã‚¤ãƒ«ã«é…ç½®ã—ãŸã„ä½ç½®ã‚’è¨˜è¿°ã—ã¾ã™
                        PosY = PosY + class3__items[i].clientHeight + marginBottom;  // ä»Šã®Yè»¸ã®ä½ç½®ã«è¦ç´ ã®é«˜ã•ã‚’åŠ ç®—
                        console.log(PosY)
                        sh = PosY + 150
                        gridWindow.style.height = sh+'px'; // è¨ˆç®—ã—ãŸé«˜ã•ã‚’è¦ªè¦ç´ ã«æŒ‡å®šã—ã¾ã™
                    } else {
                        const oddPosX = class3__items[0].clientWidth * 495 / 485;
                        if (i % 2 == 0) { // Gridã«é…ç½®ã•ã‚Œã‚‹å¶æ•°ç•ªç›®ã®è¦ç´ ã«å¯¾ã™ã‚‹å‡¦ç†
                            class3__items[i].setAttribute("style", "transform: translateX("+ evenPosX +"px) translateY(" + evenPosY + "px)");  // ã‚¹ã‚¿ã‚¤ãƒ«ã«é…ç½®ã—ãŸã„ä½ç½®ã‚’è¨˜è¿°ã—ã¾ã™
                            evenPosY = evenPosY + class3__items[i].clientHeight + marginBottom;  // ä»Šã®Yè»¸ã®ä½ç½®ã«è¦ç´ ã®é«˜ã•ã‚’åŠ ç®—
                        } else if (i % 2 != 0) {  // Gridã«é…ç½®ã•ã‚Œã‚‹å¥‡æ•°ç•ªç›®ã®è¦ç´ ã«å¯¾ã™ã‚‹å‡¦ç†
                            class3__items[i].setAttribute("style", "transform: translateX(" + oddPosX + "px) translateY(" + oddPosY + "px)");  // ã‚¹ã‚¿ã‚¤ãƒ«ã«é…ç½®ã—ãŸã„ä½ç½®ã‚’è¨˜è¿°ã—ã¾ã™
                            oddPosY = oddPosY + class3__items[i].clientHeight + marginBottom;  // ä»Šã®Yè»¸ã®ä½ç½®ã«è¦ç´ ã®é«˜ã•ã‚’åŠ ç®—
                        }
                        evenPosY > oddPosY ? sh = evenPosY + 150 : sh = oddPosY + 150; // å…¨éƒ¨ã®è¦ç´ ãŒé…ç½®ã—çµ‚ã‚ã£ãŸã‚‰ã€è¦ªè¦ç´ ã®é«˜ã•ã‚’è¨­å®šã™ã‚‹ãŸã‚ã«è¨ˆç®—ã—ã¾ã™
                        gridWindow.style.height = sh+'px'; // è¨ˆç®—ã—ãŸé«˜ã•ã‚’è¦ªè¦ç´ ã«æŒ‡å®šã—ã¾ã™
                    }
                }
            }
        }
    },
}
</script>

