<template>
    <p v-if="cartOrders == ''" class="cart__msg">ã‚«ãƒ¼ãƒˆã«ä½•ã‚‚å…¥ã£ã¦ã„ã¾ã›ã‚“</p>
    <ListUpdate v-else class="cart__wrapper">
        <template v-for="(cartOrder, cartOrder_index) in cartOrders" :key="cartOrder_index">
            <CartShow
                class="cart__item"
                :class1="cartOrder.class1"
                :class2="cartOrder.class2"
                :class3="cartOrder.class3"
                :setmenu_values="cartOrder.setmenu_values"
                :quantity="cartOrder.quantity"
                :price="cartOrder.price"
                @quantityChange="quantityChange"
            />
        </template>
    </ListUpdate>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
import CartShow from "../molecules/CartShow"
import ListUpdate from "../transition/ListUpdate"

export default {
    components: {
        CartShow,
        ListUpdate,
    },
    data(){
        return {
            animationTime: getComputedStyle(document.documentElement).getPropertyValue('--animation-time')
        }
    },
    watch: {
        lightBox:{
            handler(){
                if(this.lightBox == 'CartMenus'){
                    setTimeout(() => 
                        this.makePageCardGridLayout(),
                    this.animationTime)
                }
            },
        },
        cartOrders:{
            handler(){
                setTimeout(() => 
                    this.makePageCardGridLayout(),
                1)
            },
            deep: true,
            immediate: true,
        },
    },
    mounted: function () {
        window.addEventListener("resize", ()=>{
            let timeoutID = 0;
            let delay = 500;
            clearTimeout(timeoutID);
            timeoutID = setTimeout(()=>{
                this.makePageCardGridLayout()
                console.log("delay");
    
            }, delay);
        }, false);
    },
    computed: {
        ...mapGetters([ 'cartOrders' ,'selectClass', 'lightBox']),
    },
    methods: {
        ...mapActions([ 'addCart', 'cartBallPositionUpdate' ]),
        quantityChange(event){
            console.log(event)
            this.addCart(event)
        },
        makePageCardGridLayout() {
            console.log('makePageCardGridLayout')
            let evenPosX = 0;
            let evenPosY = 0;
            let oddPosY = 0;
            const PosX = 0;
            let PosY = 0;
            let sh
            const marginBottom = 25
            const cart__items = document.getElementsByClassName('cart__item');
            if(cart__items.length > 0) {
                let gridWindow;
                for (let i = 0; i < cart__items.length; i++) {
                    i == 0 ? gridWindow = cart__items[0].closest('.cart__wrapper') : i; // Gridã®è¦ªè¦ç´ ã‚’å–å¾—ã—ã¾ã™
                    if (window.matchMedia('(max-width: 959px)').matches) {
                        console.log('under 959px')
                        cart__items[i].setAttribute("style", "transform: translateX(" + PosX + "px) translateY(" + PosY + "px)");  // ã‚¹ã‚¿ã‚¤ãƒ«ã«é…ç½®ã—ãŸã„ä½ç½®ã‚’è¨˜è¿°ã—ã¾ã™
                        PosY = PosY + cart__items[i].clientHeight + marginBottom;  // ä»Šã®Yè»¸ã®ä½ç½®ã«è¦ç´ ã®é«˜ã•ã‚’åŠ ç®—
                        console.log(PosY)
                        sh = PosY + 150
                        gridWindow.style.height = sh+'px'; // è¨ˆç®—ã—ãŸé«˜ã•ã‚’è¦ªè¦ç´ ã«æŒ‡å®šã—ã¾ã™
                    } else {
                        const oddPosX = cart__items[0].clientWidth * 495 / 485;
                        if (i % 2 == 0) { // Gridã«é…ç½®ã•ã‚Œã‚‹å¶æ•°ç•ªç›®ã®è¦ç´ ã«å¯¾ã™ã‚‹å‡¦ç†
                            cart__items[i].setAttribute("style", "transform: translateX("+ evenPosX +"px) translateY(" + evenPosY + "px)");  // ã‚¹ã‚¿ã‚¤ãƒ«ã«é…ç½®ã—ãŸã„ä½ç½®ã‚’è¨˜è¿°ã—ã¾ã™
                            evenPosY = evenPosY + cart__items[i].clientHeight + marginBottom;  // ä»Šã®Yè»¸ã®ä½ç½®ã«è¦ç´ ã®é«˜ã•ã‚’åŠ ç®—
                        } else if (i % 2 != 0) {  // Gridã«é…ç½®ã•ã‚Œã‚‹å¥‡æ•°ç•ªç›®ã®è¦ç´ ã«å¯¾ã™ã‚‹å‡¦ç†
                            cart__items[i].setAttribute("style", "transform: translateX(" + oddPosX + "px) translateY(" + oddPosY + "px)");  // ã‚¹ã‚¿ã‚¤ãƒ«ã«é…ç½®ã—ãŸã„ä½ç½®ã‚’è¨˜è¿°ã—ã¾ã™
                            oddPosY = oddPosY + cart__items[i].clientHeight + marginBottom;  // ä»Šã®Yè»¸ã®ä½ç½®ã«è¦ç´ ã®é«˜ã•ã‚’åŠ ç®—
                        }
                        evenPosY > oddPosY ? sh = evenPosY + 150 : sh = oddPosY + 150; // å…¨éƒ¨ã®è¦ç´ ãŒé…ç½®ã—çµ‚ã‚ã£ãŸã‚‰ã€è¦ªè¦ç´ ã®é«˜ã•ã‚’è¨­å®šã™ã‚‹ãŸã‚ã«è¨ˆç®—ã—ã¾ã™
                        gridWindow.style.height = sh+'px'; // è¨ˆç®—ã—ãŸé«˜ã•ã‚’è¦ªè¦ç´ ã«æŒ‡å®šã—ã¾ã™
                    }
                }
            }
        }
    },
}
</script>

