<template>
    <nav class="menu_controller__wrapper">
        <LeftFade>
            <ul class="menu_controller" v-show="isShow == true">
                <ControllerContents
                    v-for="(content, index) in contents"
                    @click="isShow = content.isShow , lightBoxShow(content.url)"
                    :key="index"
                    :content="content.content"
                    :img="content.img"
                    :url="content.url"/>
            </ul>
        </LeftFade>
        <ControllerButton :isShow="isShow" @click="isShow = !isShow"/>
    </nav>
</template>

<script>
import { mapActions } from "vuex"
import LeftFade from "../transition/LeftFade.vue"
import ControllerButton from '../molecules/ControllerButton.vue'
import ControllerContents from '../molecules/ControllerContents.vue'

export default {
    data: () => {
        return {
            isShow: false,
            contents:[
                {content: 'ãƒ¡ãƒ‹ãƒ¥ãƒ¼ä½œæˆ', img: 'icon--create', url: "CreateMenu", isShow: false},
                {content: 'ãƒ¡ãƒ‹ãƒ¥ãƒ¼å‰Šé™¤', img: 'icon--delete', url: "", isShow: true},
                {content: 'ãƒ¡ãƒ‹ãƒ¥ãƒ¼ä¸¦ã¹æ›¿ãˆ', img: 'icon--sort', url: "", isShow: true},
                {content: 'æ³¨æ–‡å±¥æ­´', img: 'icon--history', url: "OrderHistory", isShow: false},
                {content: 'ãƒ†ãƒ¼ãƒ–ãƒ«ã«æ‹›å¾…', img: 'icon--invitation', url: "TableInvitation", isShow: false},
            ]
        }
    },
    components: {
        LeftFade,
        ControllerButton,
        ControllerContents,
    },
    methods: {
        ...mapActions([ 'lightBoxShow' ]),
        keyAction(e) {
            if (e.keyCode == 77 && e.altKey == true) {
                this.isShow = !this.isShow
            }else if(e.keyCode==49 && e.altKey == true){
                this.lightBoxShow('CreateMenu')
            }else if(e.keyCode==50 && e.altKey == true){
                this.lightBoxShow('')
            }else if(e.keyCode==51 && e.altKey == true){
                this.lightBoxShow('')
            }else if(e.keyCode==52 && e.altKey == true){
                this.lightBoxShow('OrderHistory')
            }else if(e.keyCode==53 && e.altKey == true){
                this.lightBoxShow('TableInvitation')
            }
        },
    },
    created() {
        //ã‚­ãƒ¼ã‚³ãƒ¼ãƒ‰ã«ã‚ˆã‚‹å‹•ä½œã®ç™»éŒ²
        window.addEventListener("keydown", this.keyAction);
    },
    beforeUnmount() {
        //ã‚­ãƒ¼ã‚³ãƒ¼ãƒ‰ã«ã‚ˆã‚‹å‹•ä½œã®å‰Šé™¤
        window.removeEventListener("keydown", this.keyAction);
    },
}
</script>

